import React from 'react';
import { Row, Col } from 'reactstrap';

import styles from './eExplainerVideos.module.scss';

import images from './eExplainerVideos.images';

const title = 'The 4 E’S OF EXPLAINER VIDEOS';
const subTitle = 'How animated explainer videos add values for your customer';
const date = '27/04/2020';

const data = [{
  title: 'ENLIGHTEN',
  desc: `The most crucial part of any business
is reaching clients with a laser-targeting
message. That’s exactly what a good
explainer video can achieve within a
short amount of time.`,
  img: images.enlighten,
  className: 'd-flex flex-column justify-content-end',
}, {
  title: 'ENGAGE',
  desc: `Attention spans are just eight seconds
nowadays. This means that you only have
a small amount of time to arouse your
prospect’s interest. By enhancing your
information with motion and sound design,
it will naturally keep your audience interested
with surprising informative content.`,
  img: images.engage,
  imgClassName: 'd-flex flex-column justify-content-end',
}, {
  title: 'EXPOUND',
  desc: `Explainer videos enable you to elaborate on
topics that can be hard to explain with words 
alone. Attention-grabbing animated video
provides information in a format that
people can easily digest and remember.`,
  img: images.expound,
  imgClassName: 'd-flex flex-column justify-content-end',
}, {
  title: 'ENTICE',
  desc: `At the end of it all, the main objective is
for your audience to take some sort of
action. Besides educating your audience,
explainer videos often try to entice them
with a compelling call-to-action.`,
  img: images.entice,
  className: 'd-flex flex-column justify-content-end',
}]

const eExplainerVideos = () => {
  return (
    <div className={styles.container}>
      <h1>{title}</h1>
      <h2>{subTitle}</h2>
      <p className="date">{date}</p>
      {/* <div className="seperator" /> */}
      {
        data.map((d, i) => (
          <Row className={styles.itemContainer} noGutters>
            <Col className={d.className} xs={{ order: 2, size: 12 }} sm={{ order: i % 2 ? 2 : 1, size: i % 2 ? 6 : 6 }}>
              <h3 className={i % 2 ? 'text-right' : ''}>{d.title}</h3>
              <p className={i % 2 ? 'text-right' : ''}>{d.desc}</p>
            </Col>
            <Col className={d.imgClassName} xs={{ order: 1, size: 12 }} sm={{ order: i % 2 ? 1 : 2, size: i % 2 ? 6 : 6 }}>
              <div className="image-container">
                <img {...d.img} alt={d.title} />
              </div>
            </Col>
          </Row>
        ))
      }
    </div>
  );
}

export default eExplainerVideos;
